class UI {
  constructor(user) {
    this._user = user;
    this._Init();
  }

  _Init() {
    console.log('UI');
  }

  AccounLoggedIn(user) {
    let loginBtn = document.getElementById('login-btn');
    loginBtn.style.display = 'none';

    let menuList = document.getElementById('menu-list');
    menuList.innerHTML += `<li class="menu-item"><a href="#" class="account-btn" id="account-btn">${user.uid}</a></li>`;
  }
}

export default UI;
